<template>
    <v-container fluid>
        <app-nav-bar />
        <v-row>
            <v-col class="col-4 offset-4">
                <v-form>
                    
                    <v-text-field
                    :append-icon="'fas fa-id-badge'"
                    v-model="username"
                    :rules="[rules.required, rules.min]"
                    label="Username"
                    required></v-text-field>

                    <v-text-field
                    :append-icon="show ? 'fas fa-eye' : 'fas fa-eye-slash'"
                    :type="show ? 'text' : 'password'"
                    :rules="[rules.required, rules.min]"
                    hint="At least 8 characters"
                    v-model="password"
                    label="Password"
                    @click:append="show = !show"
                    required
                    ></v-text-field>

                    <v-row> 
                        <v-col>
                            <v-btn raised class="primary" @click="submit" >Submit</v-btn>
                        </v-col>
                        <v-col>
                            <v-btn raised class="primary" @click="clear">Clear fields</v-btn>
                        </v-col>
                    </v-row>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import NavBar from './../components/NavBar';
import firebase from 'firebase';
 
export default {
    data(){
        return{
            username: '',
            password: '',
            show: false,
            err: null,
            rules:{
                required : value => !!value || 'Required.',
                min : v=> v.length >= 8 || 'Min 8 characters required.'
            }
        }
    },
    methods:{
      submit(){
          firebase
          .auth()
          .signInWithEmailAndPassword(this.username, this.password)
          .then(data =>{
              this.$router.replace({name: "gallery"})
              console.log(data)
          })
          .catch(err =>{
              this.err = err.message;
          })
      },
      clear(){
          this.username = "",
          this.password = ""
      }
    },
    components:{
        appNavBar: NavBar
    }
}
</script>
<style scoped>

</style>